.nav-bar-items {
    background: inherit;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
  }

  .nav-menu {
      display: grid;
      grid-template-columns: repeat(5,auto);
      grid-gap: 10px;
      list-style: none;
      text-align: center;
      width: 20vw;
      justify-content: end;
      margin-right: 2rem;
  }
  
  .nav-bar-item {
      background: inherit;
      color: #FEE715FF;
      border: none;
      text-decoration: none;
      padding: 0.5rem 1rem;
  }
  
  .nav-bar-item:hover {
      background-color: red;
      color: white;
      border-radius: 4px;
      transition: all 0.2s ease-out;
  }

