body {
  background: #101820FF;
}

.App {
  color: #FEE715FF;
  background: #101820FF;
}

.token-list-container {
  display: -webkit-inline-flex;
  flex-wrap: wrap;
  justify-content: first baseline;
  font-size: 12px;
}

.token-list-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 130px;
  height: auto;
  margin: 0 0 0 0;
  padding: 1.1em;
}

.token-list-image {
  width: 160%;
}

.meta-mask-button {
  align-self: center;
  float: right;
}

button {
  background: inherit;
  color: #FEE715FF;
  text-decoration: none;
  padding: 0.2rem 0.3rem;
}

button:hover {
  background-color: red;
  color: white;
  border-radius: 1px;
  transition: all 0.2s ease-out;
}